@import url(https://fonts.googleapis.com/css2?family=Londrina+Solid:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
* {
    margin:0;
    padding:0;
    font-family:"Open Sans",sans-serif;
}

body {
    background:url(/my-movies/static/media/dark-background.cbd1e21c.jpg) no-repeat fixed;
}

.logo-container {
    display:-webkit-flex;
    display:flex;
    -webkit-flex-wrap:nowrap;
            flex-wrap:nowrap;
    background-color: yellow;
}

.logo-container .logo{
    margin-right:10px;
    white-space:nowrap;
}

.logo-container img{
    width:40px;
}
.nav-container{
    display:-webkit-flex;
    display:flex;
    -webkit-align-items:center;
            align-items:center;
}

.nav-container a{
    padding:10px;
    font-weight:700;
    text-shadow:0 0 10px #fff;
    font-size:20px;
    white-space:nowrap;
}
.header a,.nav-container a{
    text-decoration:none;
    color:#fff;
}
.header a{
    font-family:"Londrina Solid",cursive;
}

.header{
    position:fixed;
    top:0;left:0;
    right:0;
    padding:20px 10px;
    -webkit-justify-content:space-around;
            justify-content:space-around;
    -webkit-align-items:center;
            align-items:center;
    z-index:10;
    background-color:#000;
    -webkit-flex-wrap:wrap;
            flex-wrap:wrap;
}
.header,.search{
    display:-webkit-flex;
    display:flex;
}

.search{
    -webkit-flex-wrap:nowrap;
            flex-wrap:nowrap;
}
.search select{
    margin-right:20px;
    padding:5px;
    border-radius:10px;
}
.promo-container{
    margin-top:70px;
    min-height:70vh;
    background-size:cover;
    background-position:50%;
    background-repeat:no-repeat;
    max-width:100vw;
    color:#fff;
    -webkit-align-items:flex-end;
            align-items:flex-end;
    padding:40px;
    background-color:grey;
    box-shadow:0 5px 10px #fff;
}
.film-info,.promo-container{
    display:-webkit-flex;
    display:flex;
}
.film-info{

    -webkit-justify-content:space-between;

            justify-content:space-between;
}
button.trailer{
    padding:10px 20px;
}
.description{
    margin-bottom:20px;
}
.film-info{
    text-shadow:0 0 5px #000;
}
.film-block{
    position:relative;
    padding:30px;
    overflow:hidden;
}
.film-block h1{
    color:#fff;
    text-shadow:0 0 10px #fff;
    font-family:"Open Sans",sans-serif;
}
.film-row{
    display:-webkit-flex;
    display:flex;
    transition:.5s;
    list-style:none;
}
.film{
    width:150px;
    height:200px;
    margin:10px;
    box-shadow:0 0 5px 3px #fff;
    transition:.6s;
}
.film:hover{
    -webkit-transform:scale(1.1);
            transform:scale(1.1);
    cursor:pointer;
}
.btn{
    position:absolute;
    width:40px;
    height:40px;
    font-size:25px;
    border-radius:50%;
    outline:none;
    border:none;
    color:#fff;
    font-size:bold;
    cursor:pointer;
    background-color:red;
    display:-webkit-flex;
    display:flex;
    -webkit-align-items:center;
            align-items:center;
    -webkit-justify-content:center;
            justify-content:center;
}
.btn-prev{
    left:0;
    top:50%;
}
.btn-next{
    right:0;
    top:50%;
}
.search-btn{
    font-size:14px;
    background-color:red;
    color:#fff;
    font-weight:700;
    cursor:pointer;
}
.search-btn,.search input{
    padding:5px 10px;
    border-radius:10px;
    outline:none;
    border:none;
}
.search input{
    margin-right:10px;
}
.list-container{
    max-width:1080px;
    width:80%;
    margin:100px auto 0;
}
.list-container h2{
    color:#fff;
    text-shadow:0 0 10px #fff;
    font-family:"Open Sans",sans-serif;
}
.list{
    list-style:none;
}
.list a{
    text-decoration:none;
    color:#000;
}
.list .item{
    display:-webkit-flex;
    display:flex;
    margin:10px 0;
    border:1px solid #000;
    padding:10px;
    background-color:hsla(0,0%,100%,.5);
    box-shadow:0 0 10px #000;
}
.rating{
    margin:10px 0;
    max-width:100px;
    text-align:center;
    border-radius:10px;
    text-shadow:0 0 2px #000;
    padding:5px;
    background-color:#ff0;
}
.list .item button,.rating{
    color:#fff;
    box-shadow:0 0 5px #000;
}
.list .item button{
    max-width:200px;
    padding:10px 20px;
    border:none;
    outline:none;
    font-size:18px;
    cursor:pointer;
    border-radius:10px;
    background-color:#32cd32;
    white-space:nowrap;
}
.list .item button:active{
    background-color:#00f;
    color:#000;
}
.info-block{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction:column;
            flex-direction:column;
}
.info-block h2{
    text-shadow:0 0 2px #000;
    font-size:30px;
}
.active{
    background-color:red;
    border-radius:10px;
}
.film-image{
    width:150px;
    height:200px;
    margin:10px;
    box-shadow:0 0 3px 1px #000;
}
.name{
    max-width:800px;
}
.favourites{
    max-width:1080px;
    width:80%;
    margin:100px auto 0;
}
.favourites .item{
    display:-webkit-flex;
    display:flex;margin:10px 0;
    border:1px solid #000;
    padding:10px;
    background-color:hsla(0,0%,100%,.5);
    box-shadow:0 0 10px #000;
    transition:.5s;
}
.favourites h2,.favourites h3,.list-container h3{
    color:#fff;
    text-shadow:0 0 10px #fff;
    font-family:"Open Sans",sans-serif;
}
.favourites h3,.list-container h3{
    margin:40px 0;
    text-align:center;
    font-size:30px;
}
.favourites .item button{
    max-width:200px;
    padding:10px 20px;
    border:none;
    outline:none;
    font-size:18px;
    cursor:pointer;
    border-radius:10px;
    color:#fff;
    background-color:red;
    box-shadow:0 0 5px #000;
    white-space:nowrap;
}
.favourites .item button:active{
    background-color:#00f;
    color:#000;
}
.card_item_container{
    background-size:cover;
    background-attachment:fixed;
    max-width:100vw;
    width:100%;
}
.card_item,.card_item_container{
    display:-webkit-flex;
    display:flex;
}
.card_item{
    margin-top:80px;
    padding:50px;
    background-color:hsla(0,0%,100%,.8);
    box-shadow:0 0 10px 2px #fff;
    -webkit-flex-direction:column;
            flex-direction:column;
}
.card_item_info{
    display:-webkit-flex;
    display:flex;
    margin-bottom:50px;
    text-shadow:0 0 2px #000;
}
.card_item_info .info_block{
    -webkit-justify-content:start;
            justify-content:start;
}
.card_item_info img{
    max-width:300px;
    width:100%;
    max-height:500px;
    height:100%;
}
.card_item iframe{
    -webkit-align-self:center;
    align-self:center;
    margin-bottom:50px;
}
.comment_item{
    display:-webkit-flex;
    display:flex;
    margin:20px 10px;
    padding:10px 20px;
    background-color:#fff
}
.comment_item img{
    width:100px;
    height:100px;
    margin-right:30px;
    box-shadow:0 0 5px #000;
}
.comment_email{
    padding:10px;
    background-color:#00f;
    border-radius:10px;
    color:#fff;
    box-shadow:0 0 5px #000;
}
.comment_author_name{
    padding:5px;
    text-shadow:0 0 5px #000;
}
.comment_block h2{
    text-shadow:0 0 3px #000;
}
.comment_text_block{
    padding:10px;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction:column;
            flex-direction:column;
    -webkit-align-items:flex-start;
            align-items:flex-start;
}
.comment_text_block p{
    margin:10px 0;
}
.comment_form{
    margin:20px 10px;
    background-color:#fff;
    padding:20px;
    max-width:400px;
}
.comment_form,.comment_form label{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction:column;
            flex-direction:column;
}
.comment_form label{
    margin:15px 0;
}
.comment_form input{
    padding:10px;
}
.comment_form_btn{
    max-width:100px;
    padding:10px 20px;
    background-color:green;
    font-weight:700;
    color:#fff;
}
@media (max-width:1024px){
    .header{
        -webkit-flex-direction:column;
                flex-direction:column;
        -webkit-align-items:center;
                align-items:center;
    }
    .header div{
        margin-bottom:20px;
    }
.favourites,.list-container{
    
    margin-top:250px;
}
.card_item_container,.promo-container{
    margin-top:180px;
}
}

@media (max-width:768px){
    .info-block h2{
        font-size:20px;
    }
    .description,.favourites .item button,.list .item button{
        font-size:14px;
    }

}

@media (max-width:480px){

    .favourites .item,.list .item {

    -webkit-flex-direction:column;

            flex-direction:column;
    -webkit-align-items:center;
            align-items:center;

}
}
